"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ChevronLeft, Loader2, CheckCircle2, ShieldCheck } from "lucide-react"
import Image from "next/image"

// 기관 정보 데이터 (코드는 내부적으로만 사용)
const INSTITUTIONS = [
  { id: "0088", name: "신한은행", logo: "./lib/images/shin.png" }, // 실제 경로에 맞게 이미지 필요
  { id: "0004", name: "국민은행", logo: "/lib/images/kuk.png" },
  { id: "0003", name: "기업은행", logo: "/lib/images/ki.png" },
]

export default function LinkAccountPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [step, setStep] = useState(1) // 1: 선택, 2: 인증/전송중, 3: 완료
  const [loadingMessage, setLoadingMessage] = useState("금융사 보안 연결 중...")
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) {
      router.push("/login")
    } else {
      setIsLoading(false)
      const storedName = localStorage.getItem("name")
      if (storedName) setUserName(storedName)
    }
  }, [router])

  // 은행 클릭 시 호출되는 함수 (orgCode를 인자로 받음)
  const handleLink = async (orgCode: string) => {
    setStep(2)
    
    try {
      // 커넥트 아이디 시뮬레이션
      const connectId = `conn_${Math.random().toString(36).substr(2, 9)}`
      setLoadingMessage("데이터를 안전하게 전송하고 있습니다...")

      // 백엔드 API 호출 (클릭한 은행의 코드가 orgCode로 들어옵니다)
      const response = await fetch("https://sparta-nextme.xyz/api/link-account", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${localStorage.getItem("accessToken")}`
        },
        body: JSON.stringify({
          orgCode: orgCode,    // 여기서 코드가 전송됨 (사용자에게는 안 보임)
          connectId: connectId,
          userName: userName
        })
      })

      if (response.ok) {
        localStorage.setItem("isLinked", "true")
        setStep(3)
      } else {
        alert("연동 중 오류가 발생했습니다. 다시 시도해 주세요.")
        setStep(1)
      }
    } catch (error) {
      console.error("Link Error:", error)
      alert("서버와 통신할 수 없습니다.")
      setStep(1)
    }
  }

  if (isLoading) return null;

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b bg-card h-16 flex items-center px-4 sticky top-0 z-50">
        <Button variant="ghost" size="icon" onClick={() => router.back()} className="mr-2">
          <ChevronLeft className="h-5 w-5" />
        </Button>
        <h1 className="text-lg font-semibold">계정 연동</h1>
      </header>

      <main className="container mx-auto max-w-md px-4 py-12">
        {step === 1 && (
          <div className="space-y-8 animate-in fade-in slide-in-from-bottom-4">
            <div className="text-center space-y-2">
              <h2 className="text-2xl font-bold">은행 선택</h2>
              <p className="text-muted-foreground">{userName}님의 자산 분석을 위해 은행을 선택해 주세요.</p>
            </div>

            <div className="grid gap-4">
              {INSTITUTIONS.map((bank) => (
                <Button
                  key={bank.id}
                  variant="outline"
                  className="h-24 justify-between px-6 bg-card hover:border-primary hover:bg-primary/5 transition-all group border-2"
                  onClick={() => handleLink(bank.id)} // 클릭 시 코드를 함수로 전달
                >
                  <div className="flex items-center gap-4">
                    <div className="relative w-12 h-12 overflow-hidden rounded-full border bg-white flex items-center justify-center p-1">
                      {/* 실제 이미지가 없을 경우를 대비해 alt 텍스트와 폰트 크기 조정 */}
                      <Image 
                        src={bank.logo}
                        alt={bank.name} 
                        width={40} 
                        height={40} 
                        className="object-contain"
                        onError={(e) => {
                          // 이미지 로드 실패 시 대체 아이콘 처리를 위한 로직을 여기에 추가할 수 있습니다.
                        }}
                      />
                    </div>
                    <span className="text-lg font-bold text-foreground">{bank.name}</span>
                  </div>
                  <CheckCircle2 className="h-6 w-6 text-muted-foreground/30 group-hover:text-primary transition-colors" />
                </Button>
              ))}
            </div>

            <div className="p-4 rounded-xl bg-muted/50 border flex gap-3">
              <ShieldCheck className="h-5 w-5 text-primary shrink-0 mt-0.5" />
              <p className="text-xs text-muted-foreground leading-relaxed">
                금융기관 연동은 암호화된 보안 채널을 통해 안전하게 진행됩니다. 유저님의 개인정보는 동의 없이 외부에 제공되지 않습니다.
              </p>
            </div>
          </div>
        )}

        {/* STEP 2 & 3 로직은 이전과 동일하되 디자인 일관성 유지 */}
        {step === 2 && (
          <div className="py-20 text-center space-y-6 animate-in zoom-in-95">
            <div className="flex justify-center">
              <Loader2 className="h-16 w-16 animate-spin text-primary" />
            </div>
            <div className="space-y-2">
              <h2 className="text-xl font-bold">인증 정보 전송 중</h2>
              <p className="text-muted-foreground">{loadingMessage}</p>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="py-20 text-center space-y-8 animate-in zoom-in-95">
            <div className="flex justify-center">
              <div className="h-20 w-20 rounded-full bg-primary/10 flex items-center justify-center">
                <CheckCircle2 className="h-12 w-12 text-primary" />
              </div>
            </div>
            <div className="space-y-2">
              <h2 className="text-2xl font-bold">연동 완료!</h2>
              <p className="text-muted-foreground">
                {userName}님의 금융 데이터 연동이 완료되었습니다.
              </p>
            </div>
            <Button size="lg" className="w-full" onClick={() => router.push("/dashboard")}>
              대시보드로 돌아가기
            </Button>
          </div>
        )}
      </main>
    </div>
  )
}